import React, { useState } from 'react';
import { Formik, Field, Form, ErrorMessage } from 'formik';
import * as Yup from 'yup';
import { accountService } from '../../views/services/account.service';
import { alertService } from '../../views/services/alert.service';
import { Col, Row, FormGroup, Label, Input, CustomInput, Button } from 'reactstrap';

const ProfileEditForm = ({ history, user, setUserEdit }) => {

    const onChange = (e) => {
        e.persist();
        setUserEdit(e.target.value, e.target.name);
    };
    const initialValues = {
        username: user.firstName,
        email: user.email,
        firstName: user.firstName,
        lastName: user.lastName,
        password: '',
        confirmPassword: '',
    };


    const [isDeleting, setIsDeleting] = useState(false);
    function onDelete() {
        setIsDeleting(true);
        accountService.delete(user.id).then(() => alertService.success('Account deleted successfully'));
    }

    const validationSchema = Yup.object().shape({
        firstName: Yup.string().required('First Name is required'),
        lastName: Yup.string().required('Last Name is required'),
        email: Yup.string().email('Email is invalid').required('Email is required'),
        password: Yup.string().min(6, 'Password must be at least 6 characters'),
        confirmPassword: Yup.string()
            .when('password', (password, schema) => {
                if (password) return schema.required('Confirm Password is required');
            })
            .oneOf([Yup.ref('password')], 'Passwords must match'),
    });

    function onSubmit(fields, { setStatus, setSubmitting }) {
        console.log(setStatus());
        setStatus();
        accountService
            .update(user.id, fields)
            .then(() => {
                alertService.success('Update successful', { keepAfterRouteChange: true });
                history.push('.');
                console.log('then');
            })
            .catch((error) => {
                setSubmitting(false);
                alertService.error(error);
                console.log('catch: ');
            });
    }

    return user.role === "user from Google" ? (
        <Formik onSubmit={onSubmit}>
            {({ errors, touched, isSubmitting }) => (
                <Form>
                    <Row form className='p-5'>
                        <Col md={6}>
                            <FormGroup>
                                <Label for='email'>Email</Label>
                                <Input
                                    name='email'
                                    value={user.email}
                                    type='text'
                                    className={'form-control' + (errors.email && touched.email ? ' is-invalid' : '')}

                                />
                                <ErrorMessage name='email' component='div' className='invalid-feedback' />
                            </FormGroup>
                        </Col>

                        <Col md={6}>
                            <FormGroup>
                                <Label for='firstName'>First name</Label>
                                <Field
                                    name='firstName'
                                    type='text'
                                    placeholder={user.firstName}
                                    className={
                                        'form-control' + (errors.firstName && touched.firstName ? ' is-invalid' : '')
                                    }
                                    onChange={onChange}
                                />
                                <ErrorMessage name='firstName' component='div' className='invalid-feedback' />
                            </FormGroup>
                        </Col>

                        <Col md={6}>
                            <FormGroup>
                                <Label for='lastName'>Last name</Label>
                                <Field
                                    name='lastName'
                                    placeholder={user.lastName}
                                    type='text'
                                    className={
                                        'form-control' + (errors.lastName && touched.lastName ? ' is-invalid' : '')
                                    }
                                    onChange={onChange}
                                />
                                <ErrorMessage name='lastName' component='div' className='invalid-feedback' />
                            </FormGroup>
                        </Col>
                    </Row>
                </Form>
            )}
        </Formik>

    ) : (
        <Formik initialValues={initialValues} onSubmit={onSubmit}>
            {({ errors, touched, isSubmitting }) => (
                <Form>
                    <Row form className='p-5'>
                        <Col md={6}>
                            <FormGroup>
                                <Label for='email'>Email</Label>
                                <Field
                                    name='email'
                                    placeholder={user.email}
                                    type='text'
                                    className={'form-control' + (errors.email && touched.email ? ' is-invalid' : '')}
                                />
                                <ErrorMessage name='email' component='div' className='invalid-feedback' />
                            </FormGroup>
                        </Col>

                        <Col md={6}>
                            <FormGroup>
                                <Label for='firstName'>First name</Label>
                                <Field
                                    name='firstName'
                                    type='text'
                                    placeholder={user.firstName}
                                    className={
                                        'form-control' + (errors.firstName && touched.firstName ? ' is-invalid' : '')
                                    }
                                />
                                <ErrorMessage name='firstName' component='div' className='invalid-feedback' />
                            </FormGroup>
                        </Col>

                        <Col md={6}>
                            <FormGroup>
                                <Label for='lastName'>Last name</Label>
                                <Field
                                    name='lastName'
                                    placeholder={user.lastName}
                                    type='text'
                                    className={
                                        'form-control' + (errors.lastName && touched.lastName ? ' is-invalid' : '')
                                    }
                                    onChange={onChange}
                                />
                                <ErrorMessage name='lastName' component='div' className='invalid-feedback' />
                            </FormGroup>
                        </Col>
                        <Col md={12}>
                            <FormGroup>
                                <Label className='pt-3'>
                                    Change Password &nbsp;
                                    <i>(Leave blank to keep the same password)</i>
                                </Label>
                            </FormGroup>
                        </Col>
                        <Col md={6}>
                            <FormGroup>
                                <Label for='examplePassword'>New password</Label>
                                <Input type='password' name='password' placeholder='*************' />
                            </FormGroup>
                        </Col>
                        <Col md={6}>
                            <FormGroup>
                                <Label for='examplePassword'>Repeat new password</Label>
                                <Input type='password' name='password' placeholder='*************' />
                            </FormGroup>
                        </Col>
                    </Row>

                    {/* ------------------------------------------------------------ */}
                    <Button type='submit' color='primary' className='outline-hover--effect'>
                        Save changes
                    </Button>
                </Form>
            )}
        </Formik>

    )
}


export default ProfileEditForm;
