import React from 'react';
import { Col, FormGroup, Label, Input } from 'reactstrap';
const BasicInfoModal = (props) => {


  const handleChanged = (e) => {
    e.persist();
    props.setUserBasicInfo(e.target.value, e.target.name)
  };


  return (
    <>
      <FormGroup row>
        <Label for="exampetext" sm={3}>
          First name :
        </Label>
        <Col sm={9}>
          <Input
            type="text"
            name="firstName"
            id="exampleEmail"
            placeholder="Please insert your first name"
            onChange={handleChanged}
            className="border-top-0 border-left-0 border-right-0 boder-bottom"
          />
        </Col>
      </FormGroup>
      <FormGroup row>
        <Label for="exampetext" sm={3}>
          Last name :
        </Label>
        <Col sm={9}>
          <Input
            type="text"
            name="lastName"
            id="exampleEmail"
            placeholder="Please insert your last name"
            onChange={handleChanged}
            className="border-top-0 border-left-0 border-right-0 boder-bottom"
          />
        </Col>
      </FormGroup>
      <FormGroup row>
        <Label for="exampetext" sm={3}>
          Job title :
        </Label>
        <Col sm={9}>
          <Input
            type="text"
            name="jobTitle"
            id="exampleEmail"
            placeholder="Please insert your job title"
            onChange={handleChanged}
            className="border-top-0 border-left-0 border-right-0 boder-bottom"
          />
        </Col>
      </FormGroup>
      <FormGroup row>
        <Label for="exampleEmail" sm={3}>
          Location :
        </Label>
        <Col sm={9}>
          <Input
            type="text"
            name="location"
            id="exampleEmail"
            onChange={handleChanged}
            placeholder="Please insert your location"
            className="border-top-0 border-left-0 border-right-0 boder-bottom"
          />
        </Col>
      </FormGroup>
    </>
  );
};

export default BasicInfoModal;
